body {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  gap: 200px;
  background-color: rgb(127, 18, 142);
}

.fxemoji-bolt {
  height: 44px;
  left: 114px;
  position: absolute;
  top: 649px;
  width: 40px;
}

.img-lady {
  border-radius: 189px;
  margin: 30px;
}

.section-1 h1 {
  color: white;
  margin-left: 50px;
}

.section-1 p {
  color: white;
  margin-left: 50px;
  font-size: 60px;
  margin-top: -50px;
}

.section-2 {
  width: 500px;
  background-color: white;
  padding: 30px;
  border-radius: 19px;
  margin: 20px;
}

.section-2 span {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  gap: 20px;
}

.bvn-text{
  color: rgb(127, 18, 142);
}

.section-2 p {
  
  font-family: "Gotham", sans-serif;
  font-weight: bold;
}

.section-2 img {
  margin-top: -10px;
}

.section-2 .frame1 {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 50px;
  position: relative;
}

.section-2 .text-wrapper {
  color: #1e2e3f;
  font-family: "Gotham", sans-serif;
  font-size: 28px;
  font-weight: 700;
  margin-top: -1px;
  position: relative;
  white-space: nowrap;
  width: fit-content;
}

.section-2 .group {
  align-self: stretch;
  height: 311px;
  margin-right: -2px;
  position: relative;
  width: 100%;
}
@font-face {
  font-family: 'Gotham';
  src: url('../../Gotham-Font/Gotham-Light.otf') format('otf'),
      
       url('../../Gotham-Font/GothamLight.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}


@font-face {
  font-family: 'Gotham';
  src: url('../../Gotham-Font/Gotham-Black.otf') format('otf'),
      
       url('../../Gotham-Font/GothamBold.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}